// Operations.
import createLocalStages from './local/removeMedia'
import removeValidate from './local/validate'

import webrtcRemoveMediaOperation from './webrtcRemoveMedia'
import rollbackOperation from './rollbackRemoveMedia'
import basicValidate from './local/validateBasic'

import createRemoteStages from './remote/remoteOffer'
import remoteAnswer from './local/remoteAnswer'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.removeMedia', () => {
    const localStages = createLocalStages(bottle.container)
    const remoteStages = createRemoteStages(bottle.container)

    return {
      local: {
        // Stages of local operation.
        validate: removeValidate,
        localOffer: localStages.localChanges,
        signalling: localStages.signalling,
        remoteAnswer: remoteAnswer(bottle.container),
        rollback: rollbackOperation(bottle.container)
      },
      remote: {
        // Stages of remote operation.
        remoteOffer: remoteStages.localChanges,
        signalling: remoteStages.signalling
      }
    }
  })

  bottle.factory('Callstack.stages.removeBasicMedia', () => {
    const localStages = createLocalStages(bottle.container)

    return {
      local: {
        // Only the validate stage is different compared to LocalRemoveMedia.
        validate: basicValidate,
        localOffer: localStages.localChanges,
        signalling: localStages.signalling,
        remoteAnswer: remoteAnswer(bottle.container),
        rollback: rollbackOperation(bottle.container)
      }
    }
  })

  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.removeMedia', () => {
    const Callstack = bottle.container.Callstack
    const negFactory = Callstack.models.Negotiation

    function removeMedia(isLocal) {
      /**
       * Factory function for a RemoveMedia operation.
       * @method removeMedia
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of the removeMedia operation.
       */
      return callId => {
        const stages = Callstack.stages.removeMedia[isLocal ? 'local' : 'remote']
        return negFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.REMOVE_MEDIA,
            isLocal,
            // Operation methods.
            stages
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: removeMedia(true),
      remote: removeMedia(false)
    }
  })

  bottle.factory('Callstack.operations.removeBasicMedia', () => {
    const negFactory = bottle.container.Callstack.models.Negotiation

    /**
     * Factory function for a LocalRemoveBasicMedia operation.
     *    An "remove basic media" operation is identical to an "remove media" operation
     *    except that it has stricter "validate" rules.
     * @method LocalRemoveBasicMedia
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a local basic removeMedia operation.
     */
    function LocalRemoveBasicMedia(callId) {
      return negFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.REMOVE_BASIC_MEDIA,
          isLocal: true,
          // Operation methods.
          stages: bottle.container.Callstack.stages.removeBasicMedia.local
        },
        {
          callId
        }
      )
    }

    return {
      local: LocalRemoveBasicMedia
      // Note: There is no remote RemoveBasicMedia operation. It's handled as a
      //    remote RemoveMedia operation.
    }
  })

  bottle.factory('Callstack.utils.webrtcRemoveMedia', () => webrtcRemoveMediaOperation(bottle.container))
  bottle.factory('Callstack.utils.rollbackRemoveMedia', () => rollbackOperation(bottle.container))
}
