// Operations.
import createStartStages from './start'
import remoteFinish from './finish'

import createLocal from '../../../callManager/flows/LocalNegotiation'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.slowStart', () => {
    const startStages = createStartStages(bottle.container)

    return {
      remote: {
        // Stages of remote operation.
        localOffer: startStages.localChanges,
        signalling: startStages.signalling,
        remoteAnswer: remoteFinish(bottle.container)
      }
    }
  })
  // Remote-only "slow start" operation.
  bottle.factory('Callstack.operations.slowStart', () => {
    const negFactory = bottle.container.Callstack.models.Negotiation

    /**
     * Factory function for a RemoteSlowStart operation.
     * @method RemoteSlowStart
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a remote hold operation.
     */
    function RemoteSlowStart(callId) {
      return negFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.SLOW_START,
          isLocal: false,
          // Operation methods.
          stages: bottle.container.Callstack.stages.slowStart.remote,
          /*
           * Special-case operation:
           * Slow-start is a remote operation, but follows the local negotiation flow.
           */
          createFlow: (container, operation, stages) => {
            const flow = createLocal(container, operation, stages)
            return {
              ...flow,
              // Additionally, convert the `start` function parameters from the
              //     "remote negotiation" to the "local negotiation" signature.
              start: (call, opInfo, ...params) => {
                return flow.start(call, params)
              }
            }
          }
        },
        {
          callId
        }
      )
    }

    return {
      remote: RemoteSlowStart
    }
  })
}
