import { cloneDeep } from 'lodash/fp'
import name from './name'

export function getConnectionState(state) {
  return cloneDeep(state[name])
}

/**
 * Retrieves the config options provided by the connectivity plugin.
 * @method getConnectivityConfig
 * @return {Object}
 */
export function getConnectivityConfig(state) {
  return state.config[name]
}

/**
 * Retrieves the checkConnectivity property from state
 * @method getCheckConnectivity
 * @param {Object} state Redux state.
 * @return {Object}
 */
export function getCheckConnectivity(state) {
  // TODO: Double-check this state property. It's not nested under [platform] like the rest?
  return state[name].checkConnectivity
}
