import { openWebsocket, cleanupWebSocket } from '../common/websocket'
import { WS_STATUS } from './constants'

import eventEmitter from 'rbbn-emitter'

/**
 * Component for managing a websocket resource.
 * @returns {Object} The websocket manager.
 */
export default function createWebsocketManager() {
  // The current status of the websocket.
  let socketStatus = WS_STATUS.NO_SOCKET
  // The websocket object, when it is connected.
  let websocket
  let connectInterrupt = false
  const emitter = eventEmitter()
  let pingInterval

  /**
   * Getter method for websocket status.
   * @method getStatus
   * @returns {string} The current status of the websocket.
   */
  function getStatus() {
    return socketStatus
  }

  /**
   * Connects a websocket using the provided address parameters.
   * @method connect
   * @param {Object} wsInfo Websocket configuration options.
   * @param {string} wsInfo.protocol Websocket protocol to use.
   * @param {string} wsInfo.server Websocket hostname.
   * @param {string} wsInfo.port Websocket port to use.
   * @param {string} wsInfo.url Websocket notification channel.
   * @param {Object} [wsInfo.params] A map of key/value pairs of URL params to attach to the websocket.
   * @returns {Promise} A promise that resolves when the websocket is connected.
   */
  function connect(wsInfo) {
    return new Promise((resolve, reject) => {
      if (getStatus() !== WS_STATUS.NO_SOCKET) {
        reject(new Error('Websocket already exists.'))
        return
      }
      socketStatus = WS_STATUS.CONNECTING

      const wsProm = openWebsocket(wsInfo)

      wsProm
        .then(ws => {
          socketStatus = WS_STATUS.CONNECTED
          websocket = ws

          // If the interrupt flag is true, disconnect the websocket immediately.
          if (connectInterrupt) {
            connectInterrupt = false
            disconnect()
            throw new Error('Websocket connection interrupted by request.')
          }

          ws.onmessage = message => {
            let data
            // Handle messages that are either strings or stringified objects.
            try {
              data = JSON.parse(message.data)
            } catch (err) {
              data = message.data
            }

            emitter.emit('message', data)
          }

          ws.onclose = data => {
            // Clean-up internally, then report externally.
            disconnect()
            emitter.emit('close', data)
          }

          ws.onerror = err => {
            // Clean-up internally, then report externally.
            disconnect()
            emitter.emit('error', err)
          }

          resolve()
        })
        .catch(err => {
          socketStatus = WS_STATUS.NO_SOCKET
          reject(err)
        })
    })
  }

  /**
   * Disconnects the websocket.
   * @method disconnect
   * @return {undefined}
   * @throws {Error} If no websocket is connected.
   */
  function disconnect() {
    const status = getStatus()
    if (status === WS_STATUS.CONNECTED) {
      // Stop pinging.
      if (pingInterval) {
        setPinging(undefined)
      }

      // Clean-up websocket.
      if (websocket) {
        cleanupWebSocket(websocket)
        websocket = undefined
      }
      socketStatus = WS_STATUS.NO_SOCKET
    } else if (status === WS_STATUS.CONNECTING) {
      // Set the interrupt flag to true.
      connectInterrupt = true
    } else {
      throw new Error('Websocket is not connected.')
    }
  }

  /**
   * Method to start/stop the websocket pinging behaviour.
   * @method setPinging
   * @param {Object} [options]
   * @param {string} [options.message] The message to be sent as a ping.
   * @param {number} [options.interval] The interval, in milliseconds, to send pings on.
   * @example
   * // Start pinging:
   * wsManager.setPinging({ message: 'ping!', interval: 30000 })
   *
   * // Stop pinging:
   * wsManager.setPinging(undefined)
   */
  function setPinging(options) {
    if (getStatus() !== WS_STATUS.CONNECTED) {
      // A websocket is required when trying to change pinging.
      throw new Error('Websocket is not connected.')
    } else if (pingInterval) {
      // Pinging is on-going.

      if (options) {
        // Can't change pinging options once it has started.
        throw new Error('Websocket is already pinging.')
      } else {
        // Clean-up the interval when stopping the pings.
        stopPinging()
        return
      }
    } else if (!options.message || !options.interval) {
      throw new Error('All options must be provided to start pinging.')
    }

    /**
     * Method for sending a ping and handling possible errors.
     */
    function sendPing() {
      // If WS has disconnected since the last interval, stop pinging quietly.
      if (getStatus() !== WS_STATUS.CONNECTED) {
        stopPinging()
      }

      try {
        if (websocket && websocket.readyState === 1) {
          websocket.send(options.message)
        } else {
          // Throw an error so all failure scenarios are handled in `catch`.
          throw new Error('websocket was not in readyState')
        }
      } catch (err) {
        // Websocket is disconnected; stop pinging loudly.
        stopPinging()
        disconnect()
        emitter.emit('disconnected', err)
      }
    }

    /*
     * Method for cleaning-up the ping interval.
     */
    function stopPinging() {
      clearInterval(pingInterval)
      pingInterval = undefined
    }

    pingInterval = setInterval(sendPing, options.interval)
  }

  return {
    // API methods.
    connect,
    disconnect,
    setPinging,

    // Emitter methods.
    on: emitter.on,
    off: emitter.off,

    // API properties.
    get status() {
      return getStatus()
    },
    get isPinging() {
      return Boolean(pingInterval)
    }
  }
}
