import { cloneDeep } from 'lodash/fp'

/*
 * Redux state selector functions.
 */

/**
 * Gets the contacts from state.
 * @method getContacts
 * @return {Object}
 */
export function getContacts(state) {
  return cloneDeep(state.users.contacts)
}

/**
 * Gets a contact from the state whose ID matches the supplied parameter
 * @method getContact
 * @return {Object}
 */
export function getContact(state, id) {
  return cloneDeep(state.users.contacts[id])
}

/**
 * Gets the users from state.
 * @method getUsers
 * @return {Array<User>} An array of all the User objects.
 */
export function getUsers(state) {
  const allUsers = cloneDeep(state.users.users)
  return Object.values(allUsers)
}

/**
 * Gets the users from state.
 * @method getUser
 * @return {Object}
 */
export function getUser(state, name) {
  return cloneDeep(state.users.users[name])
}
