// Connectivity plugin.
import pluginInterface from '../interface'
import { applyDefaults, checkOptions } from './configs'
import createOperations from './operations'

// Connectivity service.
import createWebsocketManager from '../wsManager'
import { WS_STATUS } from '../wsManager/constants'

// Other plugins.
import { update as updateConfig } from '../../config/interface/actions'

import { connCheckResponsibility } from '../../constants'

/**
 * Connectivity Plugin factory.
 * @param {Object} options Configurations for the plugin.
 * @param {Object} bottle
 * @returns {Object} An instance of the Connectivity plugin.
 */
export default function connectivityPlugin(bottle, options = {}) {
  options = applyDefaults(options)
  // Parse configs to log any issues with them.
  checkOptions(options)

  // For backwards compatibility, convert the old style to the new style.
  // TODO: This should be done inside `applyDefault` or similar.
  if (typeof options.method === 'string') {
    const method = options.method
    options.method = {
      type: method,
      // In both old scenarios, the client is responsible.
      responsibleParty: connCheckResponsibility.CLIENT
    }
  }

  /*
   * Add the plugin's components to the bottle. These are components that other
   *    components (eg. other plugins, APIs) need access to.
   */
  bottle.value('Connectivity', {
    ...createOperations(bottle.container, createWebsocketManager()),
    constants: {
      WS_STATUS
    }
  })

  // Add the public API to be bottle.
  bottle.factory('ConnectivityAPI', pluginInterface.api)

  /*
   * Set a "post initialize" method to be run after the SDK is setup but before
   *    the application gets access to it.
   */
  bottle.defer(container => {
    // Add the plugin's options to state.
    container.context.dispatch(updateConfig(options, pluginInterface.name))
  })

  return {
    name: pluginInterface.name,
    reducer: pluginInterface.reducer
  }
}
