import subscribeRequest from './request'
import subscribeOperation from './operation'
import subscribeFlow from './subscribeFlow'

export default function registerFeature(bottle) {
  bottle.factory('SubscriptionRequests.subscribe', container => {
    return subscribeRequest(bottle.container)
  })

  bottle.factory('SubscriptionOperations.subscribe', container => {
    return subscribeOperation(bottle.container)
  })

  bottle.factory('SubscriptionOperations.subscribeFlow', container => {
    return subscribeFlow(bottle.container)
  })
}
