import { cloneDeep } from 'lodash/fp'

/*
 * Redux state selector functions.
 */

/**
 * Retrieves the config options provided by the auth plugin.
 * @method getAuthConfig
 * @return {Object}
 */
export function getAuthConfig(state) {
  return cloneDeep(state.config.authentication)
}

/**
 * Retrieves the maximum number of times this client will attempt to subscribe, while getting a
 * 'Service Unavailable' response from backend.
 * @method getServiceUnavailableMaxRetries
 * @param {Object} state Current state object.
 * @return {number}
 */
export function getServiceUnavailableMaxRetries(state) {
  return state.config.authentication.subscription.serviceUnavailableMaxRetries
}

/**
 * Retrieves the connection information.
 * Includes the info needed to make requests.
 * @method getConnectionInfo
 * @return {Object}
 */
export function getConnectionInfo(state) {
  if (state.authentication.connection) {
    return cloneDeep(state.authentication.connection)
  } else {
    return undefined
  }
}

/**
 * Retrieves the domain of the callee address information.
 * @method getDomain
 * @return {string}
 */
export function getDomain(state) {
  const { username = '', identity = username } = getUserInfo(state)
  const [, domain = ''] = identity.split('@')
  return domain
}

/**
 * Retrieves the identity of the currently logged-in user.
 * The identity is of the form: <userName>@<domain>
 * @method getIdentity
 * @return {string}
 */
export function getIdentity(state) {
  const userInfo = getUserInfo(state)
  return userInfo.identity || userInfo.username || ''
}

/**
 * Retrieves the user information.
 * @method getUserInfo
 * @return {Object} An object whose properties are: identity & username. Identity is user's primary contact address.
 */
export function getUserInfo(state) {
  return cloneDeep(state.authentication.userInfo) || {}
}

/**
 * Retrieves info needed to make a REST request.
 * @method getRequestInfo
 * @return {Object}
 */
export function getRequestInfo(state) {
  const { subscription } = getAuthConfig(state)
  let { server } = getAuthConfig(state)
  let { userInfo } = cloneDeep(state.authentication)

  // For backwards compatibility, we need to fall back to
  // subscription for the server connection details
  if (!server) {
    server = {
      protocol: subscription.protocol,
      base: subscription.server,
      port: subscription.port,
      version: subscription.version
    }
  }

  // If user info does not exist yet, use an empty object. Requests will still fail, but they will be handled.
  if (!userInfo) {
    userInfo = {}
  }

  let requestInfo = {
    baseURL: `${server.protocol}://${server.base}:${server.port}`,
    version: server.version,
    username: userInfo.username
  }

  const connInfo = getConnectionInfo(state)
  if (connInfo && connInfo.requestOptions) {
    requestInfo.requestOptions = connInfo.requestOptions
  }

  return requestInfo
}
