// Auth
import * as actions from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'

// Subscription plugin
import { getSubscribedServices } from '../../../subscription/interface/selectors'

// State selectors
import { getAuthConfig, getUserInfo } from '../../interface/selectors'

// Errors
import BasicError, { authCodes } from '../../../errors'

// Libraries
import base64 from 'base-64'
import utf8 from 'utf8'

export default function createOperation(container) {
  const { logManager, context, emitEvent } = container
  const log = logManager.getLogger('AUTH')

  function setCredentials({ username, password, authname, hmacToken, bearerAccessToken, anonUsername, realm }) {
    const { username: existingUsername } = getUserInfo(context.getState())
    const activeServices = getSubscribedServices(context.getState())

    if (existingUsername !== username && activeServices.length > 0) {
      // User wants to change credentials while SDK is already subscribed to some services, under different credentials.
      // Tell user to first unsubscribe.
      const error = new BasicError({
        message: 'Cannot change credentials while previous user has an active subscription.',
        code: authCodes.INVALID_STATE
      })
      context.dispatch(actions.setCredentialsFinished({ error }))

      emitEvent(eventTypes.AUTH_ERROR, { error })
      return
    }

    const methodsOfAuthentication = [password, hmacToken, bearerAccessToken].map(Boolean).filter(x => x).length
    if (username && methodsOfAuthentication >= 2) {
      // User tries to authenticate using more than one method of authentication, which is ambiguous
      const error = new BasicError({
        message:
          'Multiple methods of authentication detected. Use only one authorization method: Basic authorization (using a password) or Bearer authorization or HMAC authorization.',
        code: authCodes.INVALID_CREDENTIALS
      })
      context.dispatch(actions.setCredentialsFinished({ error }))

      emitEvent(eventTypes.AUTH_ERROR, { error })
      return
    }

    // Retrieve the connection info.
    const config = getAuthConfig(context.getState())

    // Common request options, to be used for all subsequent requests after connect.
    const requestOptions = {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      }
    }

    if (realm) {
      requestOptions.queryParams = {
        tokenrealm: realm
      }
    }

    if (username && hmacToken) {
      // Only need the hmacToken in the x-token header when subscribing.
      log.info('Setting the info for hmacToken scenario.')
      requestOptions.headers['x-token'] = hmacToken
    } else if (authname && password) {
      log.info('Setting the info for authname/password scenario.')
      requestOptions.headers.Authorization = 'Basic ' + base64.encode(utf8.encode(authname + ':' + password))
    } else if (username && bearerAccessToken) {
      log.info('Setting the info for bearerAccessToken/username scenario.')
      requestOptions.headers.Authorization = `Bearer ${bearerAccessToken}`
    } else if (username && password) {
      log.info('Setting the info for username/password scenario.')
      requestOptions.headers.Authorization = 'Basic ' + base64.encode(utf8.encode(username + ':' + password))
    } else if (username && anonUsername) {
      // Anonymous call scenario, auth headers not required.
    } else {
      log.info('Unexpected credentials scenario.')
      const error = new BasicError({
        message: 'This credentials signature is not valid.',
        code: authCodes.INVALID_CREDENTIALS
      })
      context.dispatch(actions.setCredentialsFinished({ error }))

      emitEvent(eventTypes.AUTH_ERROR, { error })
      return
    }

    // Gather information to provide externally.
    const userInfo = {
      username: anonUsername || username,
      isAnonymous: !!anonUsername
    }

    // Gather information to provide internally, to other plugins.
    const connection = {
      server: {
        server: config.server.base,
        port: config.server.port,
        protocol: config.server.protocol,
        version: config.server.version
      },
      username,
      bearerAccessToken,
      // Request options that all other (Link) plugins will use.
      requestOptions
    }

    context.dispatch(
      actions.setCredentialsFinished({
        userInfo,
        connection
      })
    )

    emitEvent(eventTypes.AUTH_CHANGE, {})
  }

  return setCredentials
}
