// Call plugin.
import { getCallByWrtcsSessionId } from '../../interface/selectors'

// Other plugins
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'

export default function createMiddleware(context) {
  const { CallManager, logManager } = context.container

  /**
   * Handle a Link "session progress" notification.
   * @method sessionProgressNotification
   */
  async function sessionProgressNotification(action) {
    if (!shouldHandlePattern(action)) {
      // Don't handle it.
      return
    }

    const message = action.payload.notificationMessage
    const { sessionData: wrtcsSessionId } = message.sessionParams

    const targetCall = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)
    if (!targetCall) {
      const log = logManager.getLogger('CALL')
      log.info(`Received ${message.eventType} notification for unknown call; ignoring.`)
      return
    }

    const notificationParams = message.callNotificationParams || {}
    // Massage data into a generic format, instead of Link-specific.
    const params = {
      eventType: message.eventType,
      sdp: message.sessionParams.sdp,
      remoteName: notificationParams.remoteName,
      remoteNumber: notificationParams.remoteDisplayNumber,
      customParameters: message.customParameters
    }

    await CallManager.receiveEarlyMedia(wrtcsSessionId, params)
  }

  return sessionProgressNotification
}

// The `incomingCallPattern` function.
export function shouldHandlePattern(action) {
  return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'sessionProgress'
}
