// Call plugin
import { getCallById } from '../../../../../interface/selectors'

import { validateDetachedMedia, validateMediaConstraints, validateDestination } from '../../../../utils/utils'

/*
 * Pre-operation validation method.
 *
 * @param {Object} state The current state.
 * @param {string} callId The unique id representing the call instance being operated on.
 * @param {Object} participants The two participants in the call.
 * @param {string} participants.to The callee's address.
 * @param {string} participants.from The caller's address.
 * @param {Object} media The media being offered by the current answer operation.
 * @param {Object} options Any additional call-related options.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */
export default function validate(state, callId, participants, media, options) {
  const call = getCallById(state, callId)

  // Validate destination
  let error = validateDestination(call.to)

  if (!error) {
    // If no error so far, proceed to next validation
    // Validate mediaConstraints
    error = validateMediaConstraints(media)
  }

  if (!error) {
    // If no error so far, proceed to next validation
    // Validating detached media before attempting to start a call.
    // This verifies that the detached media being provided is, detached, local
    // and not already in use by a session.
    if (media.medias) {
      error = validateDetachedMedia(media.medias)
    }
  }
  return error
}
