import { cloneDeep } from 'lodash/fp'

/**
 * Retrieves information about a sip event.
 * @method getSipEventInfo
 * @param  {string} eventType
 * @return {Object}
 */
export function getSipEventInfo(state, eventType) {
  if (eventType) {
    return cloneDeep(state.sipEvents[eventType])
  } else {
    return cloneDeep(state.sipEvents)
  }
}
