import resubscribeRequest from './request'
import updateSubscriptionOperation from './operation'

export default function registerFeature(bottle) {
  bottle.factory('SubscriptionRequests.resubscribe', container => {
    return resubscribeRequest(bottle.container)
  })

  bottle.factory('SubscriptionOperations.updateSubscription', container => {
    return updateSubscriptionOperation(bottle.container)
  })
}
