// Connectivity plugin.
import * as actions from '../interface/actions'
import * as eventTypes from '../interface/eventTypes'
import { getCheckConnectivity, getConnectivityConfig, getConnectionState } from '../interface/selectors'
import { startTebAttempts } from './utils'

// WS Manager.
import { WS_STATUS } from '../wsManager/constants'

// Other plugins.
import { getConnectionInfo } from '../../auth/interface/selectors'
import BasicError, { subscriptionCodes } from '../../errors'

/**
 * Factory for the Connectivity plugin's operations.
 * @method createOperations
 * @param {Object} container All of the components that plugins have added to the bottle.
 * @param {Object} wsManager The websocket manager.
 * @returns {Object}
 */
export default function createOperations(container, wsManager) {
  const { context, emitEvent, logManager, SubscriptionOperations, NotificationsOperations } = container
  const log = logManager.getLogger('CONNECTIVITY')

  // Register listeners on the WS manager.
  wsManager.on('message', message => {
    log.debug('Received message on websocket.', message)
    onMessage(message)
  })
  wsManager.on('close', event => {
    log.debug(`Websocket closed (wasClean?: ${event.wasClean}; code ${event.code}).`)
    onClose(event)
  })
  wsManager.on('disconnected', err => {
    log.debug(`Websocket disconnected due to ping error: ${err.message}`)
    onDisconnect()
  })
  wsManager.on('error', () => {
    log.debug('Websocket disconnected due to error.')
    onDisconnect()
  })

  /**
   * @method connect
   * @async
   * @param {Object} wsInfo
   * @param {boolean} [isReconnect=false]
   * @returns {Promise<undefined>}
   * @throws {BasicError}
   */
  async function connect(wsInfo, isReconnect = false) {
    log.info(`${isReconnect ? 'Re-' : ''}Connecting to websocket.`)
    // Update state for "connection in-progress".
    context.dispatch(actions.wsAttemptConnect(wsInfo))
    emitEvent(eventTypes.WS_CHANGE, {})

    const connConfig = getConnectivityConfig(context.getState())
    let shouldCheck = getCheckConnectivity(context.getState())
    if (typeof shouldCheck === 'undefined') {
      // If the flag was not explicitly set in state, revert to the config.
      shouldCheck = connConfig.checkConnectivity
    }

    try {
      // The delay between attempts should not be shorter than 2 seconds.
      const delayTime = connConfig.reconnectDelay > 2000 ? connConfig.reconnectDelay : 2000
      log.debug(
        `Attempting ${connConfig.reconnectLimit} connections (delay: ${delayTime}ms, mult.: ${connConfig.reconnectTimeMultiplier}).`
      )

      // Start attempting to connect the websocket. Will make multiple attempts, with a delay
      //    between each attempt, until the websocket connects or we reach `maxAttempts`.
      await startTebAttempts(
        {
          taskTimeout: delayTime,
          attemptDelay: delayTime,
          delayMultiplier: connConfig.reconnectTimeMultiplier,
          maxDelay: connConfig.reconnectTimeLimit,
          maxAttempts: connConfig.reconnectLimit
        },
        wsManager.connect,
        wsInfo
      )

      if (shouldCheck) {
        log.info(`Sending websocket pings every ${connConfig.pingInterval}ms.`)
        // Start sending pings if configured to do so.
        wsManager.setPinging({
          message: JSON.stringify({ message_type: 'ping' }),
          interval: connConfig.pingInterval
        })
      }
    } catch (err) {
      log.info(`Failed to ${isReconnect ? 're-' : ''}connect websocket after ${connConfig.reconnectLimit} attempts.`)
      if (!isReconnect) {
        const error = new BasicError({
          message: err.message,
          code: subscriptionCodes.WS_CONNECTION_ERROR
        })
        context.dispatch(actions.wsConnectFinished(error))
        emitEvent(eventTypes.WS_CHANGE, {})
        throw error
      } else {
        // Throw the original error for the reconnect method to handle.
        throw err
      }
    }

    // TechDebt TODO: Proper payload object.
    context.dispatch(
      actions.wsConnectFinished({
        kandy: {
          ...connConfig,
          wsInfo
        },
        pinging: shouldCheck
      })
    )
    emitEvent(eventTypes.WS_CHANGE, {})
  }

  /**
   * @method disconnect
   * @return {undefined}
   */
  function disconnect() {
    log.info('Disconnecting websocket connection.')
    try {
      wsManager.disconnect()
    } catch (err) {
      // Even on error, handle it as a succesful disconnect.
    }

    context.dispatch(actions.wsDisconnectFinished())
    emitEvent(eventTypes.WS_CHANGE, {})
  }

  /**
   * Updates whether the websocket should be sending pings or not.
   * If there is a connected websocket, update it now. In either case, state should
   *    be updated for the next time a websocket is connected.
   * @method setPinging
   * @param {boolean} shouldPing Whether the websocket should send ping messages or not.
   * @return {undefined}
   */
  function setPinging(shouldPing) {
    if (wsManager.status === WS_STATUS.CONNECTED) {
      // Change pinging of the connected websocket.
      if (wsManager.isPinging && !shouldPing) {
        log.info('Stopping to send websocket pings.')
        // Stop pinging.
        wsManager.setPinging(undefined)
      } else if (!wsManager.isPinging && shouldPing) {
        const pingInterval = getConnectivityConfig(context.getState()).pingInterval
        log.info(`Starting to send websocket pings every ${pingInterval}ms.`)
        // Start pinging.
        wsManager.setPinging({
          message: JSON.stringify({ message_type: 'ping' }),
          interval: pingInterval
        })
      } else {
        // No change.
      }
    }
  }

  /**
   * Behaviour for handling a received websocket notification.
   * @method onMessage
   * @param {Object} message The parsed websocket mesage.
   */
  function onMessage(message) {
    // Pass the notification to the Notification plugin.
    NotificationsOperations.processNotification(message, 'WEBSOCKET')
  }

  /**
   * Behaviour for handling the websocket closing.
   * If unexpectedly, try to reconnect.
   * @method onClose
   * @param {CloseEvent} data
   * @return {undefined}
   */
  function onClose(data) {
    if (data.code === 1000) {
      log.info('Websocket closed normally.')
      // 1000 signifies a normal closure
      // Normal websocket close, don't try to reconnect
      context.dispatch(actions.wsReconnectFailed(undefined, 'link'))
      SubscriptionOperations.onConnectionLost()
    } else {
      onDisconnect()
    }
  }

  /**
   * Behaviour for handling the websocket disconnecting unexpectedly.
   * For both failing to send a message over the websocket and for the websocket
   *    closing.
   * @method onDisconnect
   * @return {Promise<undefined>}
   */
  async function onDisconnect() {
    const { wsInfo, autoReconnect } = getConnectionState(context.getState())
    log.info(`Websocket disconnected. (autoReconnect?: ${autoReconnect})`)

    context.dispatch(actions.lostConnection())
    emitEvent(eventTypes.WS_CHANGE, {})

    if (autoReconnect) {
      // If using tokens, ensure the URL is using the latest token from state.
      const { bearerAccessToken } = getConnectionInfo(context.getState())
      const connConfig = getConnectivityConfig(context.getState())
      if (bearerAccessToken && connConfig.webSocketOAuthMode === 'query') {
        wsInfo.params = {
          access_token: bearerAccessToken
        }
      }

      try {
        await connect(wsInfo, true)
      } catch (err) {
        context.dispatch(actions.wsReconnectFailed())
        SubscriptionOperations.onConnectionLost()
      }
    } else {
      context.dispatch(actions.wsReconnectFailed())
      SubscriptionOperations.onConnectionLost()
    }
  }

  return {
    connect,
    disconnect,
    setPinging
  }
}
