// Operations.
import createLocalStages from './local/unhold'
import validate from './local/validate'
import rollbackOperation from './rollbackUnhold'

import createRemoteStages from './remote/remoteOffer'
import createRemoteAnswer from './local/remoteAnswer'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  const localStages = createLocalStages(bottle.container)
  const remoteStages = createRemoteStages(bottle.container)

  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.unhold', () => {
    return {
      local: {
        // Stages of local operation.
        validate,
        localOffer: localStages.localChanges,
        signalling: localStages.signalling,
        remoteAnswer: createRemoteAnswer(bottle.container),
        rollback: rollbackOperation(bottle.container)
      },
      remote: {
        // Stages of remote operation.
        remoteOffer: remoteStages.localChanges,
        signalling: remoteStages.signalling
      }
    }
  })

  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.unhold', () => {
    const Callstack = bottle.container.Callstack
    const negFactory = Callstack.models.Negotiation

    function unhold(isLocal) {
      /**
       * Factory function for a Hold operation.
       * @method unhold
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of the hold operation.
       */
      return callId => {
        const stages = Callstack.stages.unhold[isLocal ? 'local' : 'remote']
        return negFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.UNHOLD,
            isLocal,
            // Operation methods.
            stages
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: unhold(true),
      remote: unhold(false)
    }
  })

  bottle.factory('Callstack.utils.rollbackUnhold', () => rollbackOperation(bottle.container))
}
