// The factory funnction for the notification handler.
import createLocal from './remote/callCancelled'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.callCancelled', () => {
    return {
      remote: {
        remoteOffer: createLocal(bottle.container)
        // No signalling stage; local-only.
      }
    }
  })

  bottle.factory('Callstack.operations.callCancelled', () => {
    const updateFactory = bottle.container.Callstack.models.Update

    return callId => {
      return updateFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.CALL_CANCEL,
          isLocal: false,
          stages: bottle.container.Callstack.stages.callCancelled.remote
        },
        {
          callId
        }
      )
    }
  })
}
