import * as actionTypes from './actionTypes'
import { handleActions } from 'redux-actions'
import { omit } from 'lodash/fp'

const reducers = {}

reducers[actionTypes.WS_ATTEMPT_CONNECT] = {
  next(state, action) {
    return {
      ...state,
      connected: false,
      pinging: false
    }
  }
}

reducers[actionTypes.WS_RECONNECT_FAILED] = {
  next(state, action) {
    return {
      ...state,
      connected: false,
      pinging: false
    }
  }
}

reducers[actionTypes.WS_CONNECT_FINISHED] = {
  next(state, action) {
    return {
      ...state,
      connected: true,
      pinging: action.payload.pinging,
      ...omit('checkConnectivity', action.payload.kandy)
    }
  },
  throw(state, action) {
    return {
      connected: false,
      pinging: false
    }
  }
}

reducers[actionTypes.LOST_CONNECTION] = {
  next(state, action) {
    return {
      ...state,
      connected: false,
      pinging: false
    }
  }
}

reducers[actionTypes.WS_DISCONNECT_FINISHED] = {
  next(state, action) {
    return {
      connected: false,
      pinging: false
    }
  },
  throw(state, action) {
    return {
      connected: false,
      pinging: false
    }
  }
}

reducers[actionTypes.WS_ERROR] = {
  next(state, action) {
    return {
      connected: false,
      pinging: false
    }
  },
  throw(state, action) {
    return {
      connected: false,
      pinging: false
    }
  }
}

/*
  Accepts a boolean payload for checkConnectivity
 */
reducers[actionTypes.CHANGE_CONNECTIVITY_CHECKING] = {
  next(state, action) {
    return {
      ...state,
      checkConnectivity: action.payload
    }
  }
}

/**
 * Connectivity Interface reducer
 * @method reducer
 * @param  {Object}  state - The current redux state.
 * @param  {Object}  action - A flux standard action.
 * @return {Object}  - The new redux state.
 */
const reducer = handleActions(reducers, { connected: false, pinging: false })
export default reducer
