// Local operation stages.
import createLocalStages from './local/make'
import remoteAnswer from './local/remoteAnswer'
import callRinging from '../../updates/sendRingingFeedback/remote/callStatusUpdateRinging'
import earlyMedia from './local/receiveEarlyMedia'
import validate from './local/validate'

// Remote operation stages.
import createRemoteStages from './remote/incomingCall'

import createIncoming from '../../../callManager/flows/RemoteCall'
import createNeg from '../../../callManager/flows/LocalNegotiation'
import { createPending } from '../../../callManager/flows/RemoteUpdate'

// Helpers
import setupIncomingCall from './setupIncomingCall'
import setupOutgoingSession from './setupOutgoingSession'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  const localMakeStages = createLocalStages(bottle.container)

  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.make', () => {
    return {
      local: {
        validate,
        localOffer: localMakeStages.localChanges,
        signalling: localMakeStages.signalling,
        remoteAnswer: remoteAnswer(bottle.container),
        // Remote updates while operation is pending.
        callStatusUpdateRinging: callRinging(bottle.container),
        receiveEarlyMedia: earlyMedia(bottle.container)
      },
      remote: {
        remoteOffer: createRemoteStages(bottle.container)
      }
    }
  })

  // Register the Operation as a whole entity.
  bottle.factory('Callstack.operations.make', () => {
    const Callstack = bottle.container.Callstack
    const negFactory = Callstack.models.Negotiation
    const updateFactory = Callstack.models.Update

    /**
     * Factory function for a LocalMake operation.
     * @method LocalMake
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a local make operation.
     */
    function LocalMake(callId) {
      return negFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.MAKE,
          isLocal: true,
          // Operation methods.
          stages: Callstack.stages.make.local,
          /*
           * The `make` operation is a special-case negotiation. The "main" operation
           *    follow the negotiation flow, but it can also receive updates while in PENDING
           *    state. Merge together the two flows to create the `make` flow.
           */
          createFlow: (...params) => {
            return {
              ...createNeg(...params),
              receiveUpdate: createPending(...params).start
            }
          }
        },
        {
          callId
        }
      )
    }

    function IncomingCall(callId) {
      return updateFactory.instance(
        {
          type: OPERATIONS.MAKE,
          isLocal: false,
          stages: Callstack.stages.make.remote,
          createFlow: createIncoming
        },
        {
          callId
        }
      )
    }

    return {
      local: LocalMake,
      remote: IncomingCall
    }
  })

  bottle.factory('Callstack.utils.setupIncomingCall', () => setupIncomingCall(bottle.container))
  bottle.factory('Callstack.utils.setupOutgoingSession', () => setupOutgoingSession(bottle.container))
}
