import createOperation from './onSubscriptionGone'

// Notifications Plugin
import { NOTIFICATION_RECEIVED } from '../../../../notifications/interface/actionTypes'

/**
 * Handler for when the subscription is "gone" unexpectedly.
 * This can happen in two scenarios:
 *    1. Receive a websocket notification saying the subscription is gone.
 *    2. Attempt to resubscribe the subscription, but get an error saying it does not exist.
 */
export default function registerFeature(bottle) {
  // Register the operation that handles a 'loss of connection' notification
  bottle.factory('SubscriptionOperations.onSubscriptionGone', container => {
    return createOperation(bottle.container)
  })

  // Register the listener for triggering the operation after the SDK is initialized.
  //    This ensures that the Notification plugin is setup.
  bottle.defer(container => {
    // Take notifications about the subscription being 'gone'.
    function receiveSubscriptionGoneNotificationPattern(action) {
      return (
        action.type === NOTIFICATION_RECEIVED &&
        action.payload.notificationMessage &&
        action.payload.notificationMessage.eventType === 'gone'
      )
    }

    container.Notifications.takeEveryAction(receiveSubscriptionGoneNotificationPattern, action => {
      container.SubscriptionOperations.onSubscriptionGone(action.payload.notificationMessage)
    })
  })
}
