import { name, reducer } from '../interface'
import createAPI from '../interface/api'
import createMiddleware from './middleware'
import createOperations from './operations'
import createRequests from './requests'

/**
 * Voicemail service plugin factory.
 * Provides the SDK with the Voicemail feature.
 * @method mwiFactory
 */
export default function mwiFactory(bottle) {
  // Register the component factory functions to the bottle.
  bottle.factory('MWIAPI', createAPI)
  bottle.factory('MWIOperations', createOperations)
  bottle.factory('MWIRequests', createRequests)

  const capabilities = ['voicemail']

  return {
    capabilities,
    name,
    reducer,
    middleware: createMiddleware
  }
}
