import unsubscribeRequest from './request'
import unsubscribeOperation from './operation'
import unsubscribeFlow from './unsubscribeFlow'

export default function registerFeature(bottle) {
  bottle.factory('SubscriptionRequests.unsubscribe', container => {
    return unsubscribeRequest(bottle.container)
  })

  bottle.factory('SubscriptionOperations.unsubscribe', container => {
    return unsubscribeOperation(bottle.container)
  })

  bottle.factory('SubscriptionOperations.unsubscribeFlow', container => {
    return unsubscribeFlow(bottle.container)
  })
}
