// Sip Events plugin.
import { name, reducer } from './interface'
import createAPI from './interface/api'
import createOperations from './operations'
import createRequests from './requests'
import createIntervals from './intervals'

// Other plugins.
import { NOTIFICATION_RECEIVED } from '../notifications/interface/actionTypes'

/**
 * Sip Events Plugin Factory.
 * @method sipEvents
 * @return {Object} Sip Events plugin.
 */
export default function sipEvents(bottle) {
  // Register the component factory functions to the bottle.
  bottle.factory('SipEventsAPI', createAPI)
  bottle.factory('SipEventsOperations', createOperations)
  bottle.factory('SipEventsRequests', createRequests)
  bottle.factory('SipEventsIntervals', createIntervals)

  /*
   * After the SDK has been initialized, start listening for SIP Event
   *    notifications. Route them to the `receiveEvent` operation to be handled.
   */
  bottle.defer(container => {
    // Take notification actions that MAY be for sip events.
    function receiveEventNotifyPattern(action) {
      return (
        action.type === NOTIFICATION_RECEIVED &&
        action.payload.notificationMessage.hasOwnProperty('genericNotificationParams')
      )
    }

    container.Notifications.takeEveryAction(receiveEventNotifyPattern, action => {
      container.SipEventsOperations.receiveEvent(action.payload.notificationMessage)
    })
  })

  const capabilities = ['sipEvents']

  return {
    capabilities,
    name,
    reducer
  }
}
