import createLocalStages from './local/end'
import validate from './local/validate'

import remoteOperation from './remote/callStatusUpdateEnded'

import { createEnded } from '../../../callManager/flows/RemoteUpdate'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Register the individual stages for the operation.
  bottle.factory('Callstack.stages.end', () => {
    const stages = createLocalStages(bottle.container)

    return {
      local: {
        validate,
        // TODO: This operation isn't a negotiation, so we shouldn't be naming
        //    a stage with WebRTC negotiation terms. But this is the method
        //    name the `localOperation` flow uses.
        localOffer: stages.localChanges,
        signalling: stages.signalling
      },
      remote: {
        remoteOffer: remoteOperation(bottle.container)
      }
    }
  })

  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.end', () => {
    const updateFactory = bottle.container.Callstack.models.Update

    function end(isLocal) {
      /**
       * Factory function for a End operation.
       * @method End
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of a local end operation.
       */
      return callId => {
        const stages = bottle.container.Callstack.stages.end[isLocal ? 'local' : 'remote']
        // Remote 'ended' operations require a specific flow. For local 'ended', use the default.
        const createFlow = isLocal ? undefined : createEnded
        return updateFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.END,
            isLocal,
            // Operation methods.
            stages,
            createFlow
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: end(true),
      remote: end(false)
    }
  })
}
