import commonIndex from '../core'

import basePlugins from '../core/basePlugins'
import webrtc from '../../packages/kandy/src/webrtc'
import proxy from '../../packages/kandy/src/webrtcProxy'
import subscription from '../../packages/kandy/src/subscription/link'
import auth from '../../packages/kandy/src/auth/link'
import call from '../../packages/kandy/src/call/link'
import callHistory from '../../packages/kandy/src/callHistory/link'
import clickToCall from '../../packages/kandy/src/clickToCall/link'
import connectivity from '../../packages/kandy/src/connectivity/plugin'
import messaging from '../../packages/kandy/src/messaging/link'
import mwi from '../../packages/kandy/src/mwi/link'
import notifications from '../../packages/kandy/src/notifications/link'
import presence from '../../packages/kandy/src/presence/link'
import sipEvents from '../../packages/kandy/src/sipEvents'
import users from '../../packages/kandy/src/users/link/index.js'
import { linkRequest } from '../../packages/kandy/src/request'
import '../../packages/kandy/src/docs/docs'

import { createCodecRemover } from '../../packages/kandy/src/call/utils/codecRemover.js'

const defaultPlugins = [
  ...basePlugins,
  { name: 'authentication', fn: auth },
  { name: 'subscription', fn: subscription },
  { name: 'webrtc', fn: webrtc },
  // TODO: Don't _always_ include the Proxy Plugin.
  { name: 'proxy', fn: proxy },
  { name: 'call', fn: call },
  { name: 'callHistory', fn: callHistory },
  { name: 'clickToCall', fn: clickToCall },
  { name: 'notifications', fn: notifications },
  { name: 'connectivity', fn: connectivity },
  { name: 'messaging', fn: messaging },
  { name: 'mwi', fn: mwi },
  { name: 'presence', fn: presence },
  { name: 'sipEvents', fn: sipEvents },
  { name: 'users', fn: users },
  { name: 'request', fn: linkRequest }
]

function root(options = {}, plugins = []) {
  return commonIndex(options, [...defaultPlugins, ...plugins])
}

// Alias 'create' to be equal to the root function
root.create = root

root.sdpHandlers = {
  createCodecRemover
}

// Export this way as a work-around, so it can be used as `<export>();`.
// See: https://github.com/webpack/webpack/issues/706
module.exports = root
