// Call plugin.
import { CALL_STATES } from '../../constants'
import { OPERATIONS } from '../../interface/constants'
import { getCallById, getCallByWrtcsSessionId } from '../../interface/selectors'

// Call Manager.
import { operationMap, notificationMap } from './operationMap'

import createTracker from './OperationTracker'
import registerNegotiation from './models/Negotiation'
import registerUpdate from './models/Update'
import registerMulti from './models/MultiCall'

// Other plugins.
import BasicError, { callCodes } from '../../../errors'

// Libraries.
import { v4 as uuidV4 } from 'uuid'

export default function createManager(bottle) {
  registerNegotiation(bottle)
  registerUpdate(bottle)
  registerMulti(bottle)
  bottle.factory('CallManager', () => callManager(bottle.container))
}

/**
 * @method checkCallExistence
 * @returns {undefined} Returns undefined if the call exists.
 * @throws {BasicError} Throws if the call does not exist.
 */
function checkCallExistence(state, id) {
  if (!getCallById(state, id) && !getCallByWrtcsSessionId(state, id)) {
    throw new BasicError({
      code: callCodes.INVALID_PARAM,
      message: `Call does not exist: ${id}`
    })
  }
}

export function callManager(container) {
  const { context, Callstack, CallReporter, logManager, CallRequests } = container

  // Map of calls to their on-going operations.
  const ongoing = {}

  // APIs exposed by the CallManager.
  const manager = {
    getTracker(callId) {
      return ongoing[callId]
    },
    getCallIds() {
      return Object.keys(ongoing)
    }
  }

  /**
   * CallManager API for making new, outgoing calls.
   * @method newLocalCall
   * @async
   * @param {string} callId
   * @param {Array} params
   * @return {Object|undefined}
   */
  async function newLocalCall(callId, ...params) {
    // Other than creating a call report, the "new outgoing call" flow
    //    is the same as the "local operation" flow.
    container.CallReporter.createReport('CALL', callId)

    const operation = Callstack.operations.make.local(callId)
    // Set up new call mapping for on-going operations.
    ongoing[callId] = createTracker(logManager.getLogger('CALL', callId))
    ongoing[callId].add(operation)

    return await operation.start(callId, params)
  }

  // CallManager method for receiving a new, incoming call.
  async function newRemoteCall(wrtcsSessionId, params, channel) {
    // Create an ID for the SDK to use for the call.
    const callId = uuidV4()
    container.CallReporter.createReport('CALL', callId)

    // Set up new call mapping for on-going operations.
    ongoing[callId] = createTracker(logManager.getLogger('CALL', callId))

    // Start tracking this operation.
    const operation = Callstack.operations.make.remote(callId)
    ongoing[callId].add(operation)

    params.callId = callId
    await operation.start(wrtcsSessionId, params, channel)
    ongoing[callId].remove(operation)
  }

  /**
   * CallManager method for receiving a new, remote operation.
   * @method newRemoteNegotiation
   * @async
   * @param {string} wrtcsSessionId
   * @param {Array} params
   * @throws {BasicError}
   * @return {undefined}
   */
  async function newRemoteNegotiation(wrtcsSessionId, params) {
    /**
     * Utility for determining if the new remote negotiation will cause a glare scenario or not.
     *
     * Scenario 1: The on-going negotiation is local, with a new remote negotiation received.
     *    This is a glare scenario whether we wait for the first negotiation to finish or not. The
     *    first, local negotiation will change WebRTC state which is not taken into account in the
     *    second, remote negotiation. It would cause a WebRTC error.
     *
     * Scenario 2: The on-going negotiation is remote, with another remote negotiation received.
     *    Delay processing the second negotiation until the first is finished (or until time-out.)
     *
     * Reference: KJS-1152, KJS-1918, KJS-2059
     * @method isGlare
     * @param {Object} call The up-to-date call object.
     * @param {number} [timeDelayed] The time delayed so far; only to be provided recursively.
     * @return {Promise<boolean>}
     */
    async function isGlare(call, timeDelayed = 0) {
      if (!ongoing[call.id].isBlocked) {
        return false
      } else if (timeDelayed >= 3000) {
        // Time-out after 3 seconds if it's still a glare scenario.
        return true
      }

      const { isLocal, type } = ongoing[call.id].getBlocking()
      log.debug(
        `Received new remote operation during ${isLocal ? 'local' : 'remote'} ${type} operation. Delaying handling.`
      )
      if (isLocal || type === OPERATIONS.END) {
        // If the on-going negotiation is local, then immediately respond as a glare scenario.
        //    The local negotiation will have changes that the remote side did not take into
        //    account, leading to WebRTC errors. Mark this as a glare scenario immediately so
        //    the remote side will be notified sooner.
        // If the blocking operation is an "end", also immediately respond. New operations
        //    cannot be started on ending calls.
        return true
      } else {
        // If it is two remote negotiations causing the glare scenario, try to let the first
        //    negotiation finish before processing the second.
        await new Promise(resolve => setTimeout(resolve, 250))
        return isGlare(getCallById(context.getState(), call.id), timeDelayed + 250)
      }
    }

    let call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    const log = logManager.getLogger('CALL', call ? call.id : undefined)
    log.info('Received new update call request; handling.', { wrtcsSessionId })

    if (!call) {
      // Scenario: No call is associated with the wrtcsSessionId.
      log.info('Update request is for unknown wrtcsSession. Ignoring.', { wrtcsSessionId })
      throw new BasicError({
        code: callCodes.INVALID_STATE,
        message: 'Cannot process new remote operation for call that does not exist.'
      })
    } else if (call.state === CALL_STATES.ENDED) {
      // Scenario: The associated call is ended, and should not have an active
      //    WebRTC session.
      log.info('Update request is for ended call. Ignoring.', { wrtcsSessionId })
      throw new BasicError({
        code: callCodes.INVALID_STATE,
        message: 'Cannot process new remote operation for Ended call.'
      })
    }

    let opInfo
    const isSlowStart = !params.sdp
    if (isSlowStart) {
      opInfo = { remoteOp: OPERATIONS.SLOW_START }
    } else {
      opInfo = await Callstack.utils.getRemoteOperationInfo(call, params.sdp)
    }
    const stackMethod = operationMap[opInfo.remoteOp]
    const operation = Callstack.operations[stackMethod].remote(call.id)

    if (await isGlare(call)) {
      operation.tracker.start()
      log.warn(`Received new remote operation during on-going operation; cannot process.`)
      // Call is "blocked"; starting a new negotiation is a glare scenario.
      // Therefore respond to Gateway that operation failed, so that the remote endpoint knows.
      CallRequests.updateSessionResponse({
        statusCode: 491,
        wrtcsSessionId
      })
        .then(() => {})
        .catch(error => {
          log.warn(`Failed to reply for a remote session update. Error: ${error.message}`)
        })

      // Locally, we need to mark that remote negotiation as a failure in the Call report.
      const err = new BasicError({
        code: callCodes.GLARE,
        message: `Cannot process new remote ${opInfo.remoteOp} negotiation due to glare scenario.`
      })
      operation.reportEvent.setError(err)
      operation.reportEvent.endEvent()
      operation.tracker.finish(err) // stop tracking the op because we have an error
      throw err // don't continue further
    } else {
      // Start tracking this operation.
      ongoing[call.id].add(operation)
    }

    try {
      // Get the latest call state, in case there was a delay due to glare.
      call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)
      await operation.start(call, opInfo, params)
    } catch (err) {
      ongoing[call.id].remove(operation)
      throw err
    }

    // Regular operations are finished now, but slow-start are not.
    if (!isSlowStart) {
      ongoing[call.id].remove(operation)
    }
  }

  /**
   * CallManager method for receiving the answer to an existing, local negotiation.
   * @method exiLocalNegotiation
   * @async
   * @param {string} wrtcsSessionId
   * @param {Array} params
   * @throws {BaiscError}
   * @return {undefined}
   */
  async function exiLocalNegotiation(wrtcsSessionId, params) {
    const call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)
    const log = logManager.getLogger('CALL', call ? call.id : undefined)

    /**
     * Helper to find the local operation that the received notification is a response for.
     */
    async function getExistingNegotiation(callId) {
      const call = getCallById(context.getState(), callId)
      return call.currentOperations.find((op, ind, arr) => {
        return (
          (op.isLocal && op.isBlocking) || // Local pending operation.
          (!op.isLocal && op.type === OPERATIONS.SLOW_START && op.isBlocking) || // Remote slow-start operation.
          (op.isLocal && op.type === OPERATIONS.JOIN && arr.length === 1) // Local pending join, as the only op.
        )
      })
    }

    if (!call) {
      // Scenario: The notification is about a call that state does not know about.
      //    Ignore the notification.
      log.info('Update response for unknown wrtcsSession. Ignoring.', { wrtcsSessionId })
      return
    }
    log.info('Received new update call response; handling.', { wrtcsSessionId })

    // Find which operation this "remote answer" could be for.
    const opState = await getExistingNegotiation(call.id)
    if (!opState) {
      /*
       * Scenario #1: A remote (non slow-start) operation is on-going.
       * Scenario #2: No local or remote operation is on-going.
       *    We don't know why we're receiving an answer; it doesn't match any
       *    valid on-going WebRTC negotiation where we would expect it.
       */
      log.warn('Received negotiation answer without a matching offer; ignoring.')
      return
    }

    const operation = ongoing[call.id].getByType(opState.type)
    if (!operation) {
      log.warn('Received negotiation answer without a matching offer; ignoring.')
      return
    }

    try {
      await operation.receiveResponse(call, params)
    } catch (err) {
      log.info(`Failed handling ${operation.type} response.`, err.message)
    } finally {
      // Remove the operation from the on-going list.
      ongoing[call.id].remove(operation)
    }
  }

  /**
   * Wrapper for "new, local negotiation" operations.
   * @method newLocalNegotiation
   * @param {string} stackMethod
   * @return {Function}
   */
  function newLocalNegotiation(stackMethod) {
    /**
     * CallManager API for making new, local operation call.
     * @method localOperation
     * @async
     * @param {string} callId
     * @param {Array} params
     * @throws {BasicError}
     * @return {Object|undefined}
     */
    return async function localOperation(callId, ...params) {
      checkCallExistence(context.getState(), callId)
      const operation = Callstack.operations[stackMethod].local(callId)

      if (ongoing[callId].getByType(OPERATIONS.END)) {
        // Prevent new operations when the call is ending.
        throw new BasicError({
          code: callCodes.GLARE,
          message: `Cannot start new local ${operation.type} operation while call is ending.`
        })
      } else if (operation.isNegotiation && ongoing[callId].isBlocked) {
        // Call is "blocked"; starting a new negotiation is a glare scenario.
        throw new BasicError({
          code: callCodes.GLARE,
          message: `Cannot start new local ${operation.type} negotiation due to glare scenario.`
        })
      } else {
        // Track this operation as on-going.
        ongoing[callId].add(operation)
      }

      let result
      try {
        result = await operation.start(callId, params)
      } catch (err) {
        // Operation failed; no longer on-going.
        ongoing[callId].remove(operation)
        throw err
      }

      if (!operation.isNegotiation) {
        // Local-only operation are now ended.
        ongoing[callId].remove(operation)
      }

      return result
    }
  }

  /**
   * Wrapper for generic "remote update" notifications.
   * @method newRemoteNotify
   * @param {string} stackMethod
   * @return {Function}
   */
  function newRemoteNotify(stackMethod) {
    /**
     * CallManager API for generic 'remote notifications', that aren't
     *    handled by a different API.
     * @method remoteNotify
     * @param {string} wrtcsSessionId
     * @param {Object} params
     * @return {Promise<undefined>}
     */
    return async function remoteNotify(wrtcsSessionId, params) {
      const state = context.getState()
      checkCallExistence(state, wrtcsSessionId)
      const call = getCallByWrtcsSessionId(state, wrtcsSessionId)

      const operation = Callstack.operations[stackMethod](call.id)
      ongoing[call.id].add(operation)
      await operation.start(call, params)
      ongoing[call.id].remove(operation)
    }
  }

  /**
   * Wrapper for updates to a local "make" operation update.
   * @method makeUpdate
   * @param {string} stackMethod
   * @return {Function}
   */
  function makeUpdate(stackMethod) {
    /**
     * CallManager API for 'local make' update notifications.
     * Expected Scenarios:
     *    1. Call ringing
     *    2. Early media
     * @method makeUpdate
     * @param {string} wrtcsSessionId
     * @param {Array<any>} params
     * @return {Promise<undefined>}
     */
    return async function makeUpdate(wrtcsSessionId, ...params) {
      const state = context.getState()
      checkCallExistence(state, wrtcsSessionId)
      const call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)
      const log = logManager.getLogger('CALL', call ? call.id : undefined)

      const pendingMake = ongoing[call.id].getPending().find(op => {
        return op.isLocal && [OPERATIONS.MAKE, OPERATIONS.MAKE_ANONYMOUS].includes(op.type)
      })

      if (pendingMake) {
        await pendingMake.receiveUpdate(call, stackMethod, params)
      } else {
        log.debug(`Received ${stackMethod} notification for non-make operation; ignoring.`)
      }
    }
  }

  /**
   * CallManager API for "call ended" remote notifications.
   * Expected Scenarios:
   *    1. Update received unsolicited, with no pending operation.
   *    2. Update received unsolicited, with an unrelated pending operation.
   *    3. Update received solicited (ie. with a related pending operation).
   * @method remoteCallEnded
   * @param {string} wrtcsSessionId
   * @param {Object} params Operation-specific data.
   * @return {Promise<undefined>}
   */
  async function remoteCallEnded(wrtcsSessionId, params) {
    const state = context.getState()
    checkCallExistence(state, wrtcsSessionId)
    const call = getCallByWrtcsSessionId(state, wrtcsSessionId)

    const pendingComplex = ongoing[call.id].getAll().filter(op => {
      return [OPERATIONS.CONSULTATIVE_TRANSFER, OPERATIONS.JOIN].includes(op.type)
    })[0]
    /*
     * Special-cases: The 'call end' notification was for a call in a complex operation.
     */
    if (pendingComplex) {
      const { type, calls } = pendingComplex
      if (type === OPERATIONS.CONSULTATIVE_TRANSFER && calls.some(({ role }) => role === 'secondary')) {
        /*
         * This 'end call' notification is for the secondary call of a `consultative transfer` operation, indicating a success.
         * Process the response, then stop tracking only this call's operation.
         */
        await pendingComplex.receiveResponse(call, params)
        ongoing[call.id].remove(pendingComplex)
      } else if (type === OPERATIONS.JOIN && calls.find(info => info.role === 'joined' && info.callId === call.id)) {
        /*
         * This 'end call' notification is for the joined call of a `join` operation, indicating a failure.
         * Process the response, then stop tracking the operation for all calls involved.
         */
        await pendingComplex.receiveResponse(call, params)
        calls.forEach(info => {
          ongoing[info.callId].remove(pendingComplex)
        })
      }
    } else {
      const operation = Callstack.operations.end.remote(call.id)
      ongoing[call.id].add(operation)
      const pendingOp = ongoing[call.id].getPending()[0]
      await operation.start(call, params, pendingOp)
      ongoing[call.id].remove(operation)
    }
  }

  /**
   * CallManager API for "complex response" remote notifications.
   * @method exiComplexResponse
   * @param {string} wrtcsSessionId
   * @param {Object} params Data from the notification.
   * @return {Promise<undefined>}
   */
  async function exiComplexResponse(wrtcsSessionId, params) {
    /*
     * The JOIN operation will receive a response for each call.
     * The transfers will receive a single response.
     */

    // Get the calls for both calls involved in the operation.
    const state = context.getState()
    const priCall = getCallByWrtcsSessionId(state, wrtcsSessionId)
    checkCallExistence(state, priCall.id)

    const operation = ongoing[priCall.id]
      .getAll()
      .find(
        op =>
          op.isLocal &&
          [OPERATIONS.JOIN, OPERATIONS.CONSULTATIVE_TRANSFER, OPERATIONS.DIRECT_TRANSFER].includes(op.type)
      )

    if (operation) {
      await operation.receiveResponse(priCall, params)
    } else {
      // Error: This entry should only be for a complex operation. This path indicates a bug.
      throw new Error(`Complex response entry cannot be used for notification ${params.eventType}`)
    }

    // Remove the operation from the on-going lists.
    ongoing[priCall.id].remove(operation)

    if (operation.type === OPERATIONS.JOIN && priCall.id === operation.data.priCallId) {
      // If join AND the notification was for the primary call, also stop tracking
      //    the operation for the new, joined call.
      if (ongoing[operation.data.joinedCallId].getById(operation.id)) {
        // TODO: Why is this not tracked?
        ongoing[operation.data.joinedCallId].remove(operation)
      }
    }
  }

  /**
   * Wrapper for new, local operations that act on two calls.
   * @method newDualCall
   * @param {string} stackMethod
   * @return {Function}
   */
  function newDualCall(stackMethod) {
    /**
     * @method dualCall
     * @async
     * @param {Array<Object>} calls Array of calls this operation will affect.
     * @param {Array} params
     * @throws {BasicError}
     * @return {undefined}
     */
    return async function dualCall(calls, ...params) {
      const callIds = calls.map(call => call.id)
      const [priCallId, secCallId, joinedCallId] = callIds

      checkCallExistence(context.getState(), priCallId)
      checkCallExistence(context.getState(), secCallId)
      if (joinedCallId) {
        // Create the CallReport for the joined call.
        CallReporter.createReport('CALL', joinedCallId)
      }

      const operation = Callstack.operations[stackMethod].local(calls)

      if (ongoing[priCallId].isBlocked || ongoing[secCallId].isBlocked) {
        const blockedId = ongoing[priCallId].isBlocked ? priCallId : secCallId
        // Call is "blocked"; starting a new negotiation is a glare scenario.
        throw new BasicError({
          code: callCodes.GLARE,
          message: `Cannot start new local ${operation.type} operation due to glare scenario for call ${blockedId}.`
        })
      } else {
        // Track this operation as on-going for both calls.
        ongoing[priCallId].add(operation)
        ongoing[secCallId].add(operation)

        if (operation.type === OPERATIONS.JOIN) {
          // Have the CallManager track the new call for the join operation.
          const track = createTracker(logManager.getLogger('CALL', joinedCallId))
          ongoing[joinedCallId] = track
          ongoing[joinedCallId].add(operation)

          await operation.start(calls, params)
        } else if (operation.type === OPERATIONS.CONSULTATIVE_TRANSFER) {
          await operation.start(calls)
        } else {
          // This entry should only be used for the above two operations. This path indicates a bug.
          throw new Error(`Dual-call entry cannot be used for ${operation.type}`)
        }
      }
    }
  }

  // CallManager method for making a new, outgoing call.
  manager.make = newLocalCall
  // CallManager method for receiving a new, incoming call.
  manager.establishOffer = newRemoteCall
  // CallManager method for receiving a new, remote operation.
  manager.negotiationOffer = newRemoteNegotiation
  // CallManager method for receiving the answer to a renegotiation.
  manager.negotiationAnswer = exiLocalNegotiation

  /*
   * Create CallManager methods for every local operation.
   *    eg. CallManager.hold or CallManager.reject
   */
  ;[
    // Local Negotiations.
    OPERATIONS.SLOW_ANSWER,
    OPERATIONS.HOLD,
    OPERATIONS.UNHOLD,
    OPERATIONS.ADD_MEDIA,
    OPERATIONS.REMOVE_MEDIA,
    OPERATIONS.ADD_BASIC_MEDIA,
    OPERATIONS.REMOVE_BASIC_MEDIA,
    OPERATIONS.MEDIA_RESTART,
    OPERATIONS.DIRECT_TRANSFER,
    // Local-only.
    OPERATIONS.ANSWER,
    OPERATIONS.REJECT,
    OPERATIONS.IGNORE,
    OPERATIONS.FORWARD_CALL,
    OPERATIONS.END,
    OPERATIONS.REPLACE_TRACK,
    OPERATIONS.RESYNC,
    OPERATIONS.PLAY_AUDIO,
    OPERATIONS.SEND_RINGING_FEEDBACK,
    OPERATIONS.SEND_CUSTOM_PARAMETERS,
    OPERATIONS.SEND_DTMF,
    OPERATIONS.GET_STATS
  ].forEach(type => {
    const stackMethod = operationMap[type]
    manager[stackMethod] = newLocalNegotiation(stackMethod)
  })

  /*
   * For every supported notification, create a CallManager method that routes
   *    into `remoteOnlyOps`.
   */
  ;[
    // TODO: No magic strings.
    'CALL_STATUS_RINGING',
    'RECEIVE_EARLY_MEDIA'
  ].forEach(type => {
    const stackMethod = notificationMap[type]
    manager[stackMethod] = makeUpdate(stackMethod)
  })

  manager[notificationMap['CALL_CANCELLED']] = newRemoteNotify(notificationMap['CALL_CANCELLED'])
  manager[notificationMap['CALL_STATUS_ENDED']] = remoteCallEnded

  /*
   * Handle the response notifications for complex operations.
   */
  ;['CALL_STATUS_FAILED', 'SESSION_STATUS_ENDED'].forEach(type => {
    const stackMethod = notificationMap[type]
    manager[stackMethod] = exiComplexResponse
  })

  /*
   * Create CallManager methods for two "dual call" operations.
   *    These are the complex operations that act on multiple calls at once.
   *    ie. CallManager.consultativeTransfer and CallManager.join
   */
  ;[OPERATIONS.CONSULTATIVE_TRANSFER, OPERATIONS.JOIN].forEach(type => {
    const stackMethod = operationMap[type]
    manager[stackMethod] = newDualCall(stackMethod)
  })

  return manager
}
