import statusTracker from '../StatusTracker'

import createLocal from '../flows/LocalOperation'
import { createGeneric } from '../flows/RemoteUpdate'

// Call Reports.
import { REPORTER_OPERATION_EVENTS_MAP } from '../../reporter/constants'

// Libraries.
import { v4 as uuidV4 } from 'uuid'

/**
 * Helper method to ensure the required stages are defined.
 * @method hasUpdateStages
 * @param {boolean} isLocal
 * @param {Object} stages
 * @throws {Error} When the operation is missing stages.
 */
function hasUpdateStages(isLocal, stages) {
  const { validate, localOffer, remoteOffer } = stages

  if (isLocal && !(Boolean(validate) || Boolean(localOffer))) {
    throw new Error('Missing local stage for Update operation.')
  } else if (!isLocal && !remoteOffer) {
    throw new Error('Missing remote stage for Update operation.')
  } else {
    // Operation has all the required stages for an 'Update'.
  }
}

export default function (bottle) {
  /**
   * Factory for creating instances of 'Update' operations.
   * An "Update" operation includes changes being done to the call locally
   *    and optionally a REST request.
   *
   * @param {Object} container The nested container; not actually used here.
   * @param {Object} opInfo Static info about this operation.
   * @param {string} opInfo.type Type constant of the operation.
   * @param {boolean} opInfo.isLocal Whether this operation is locall triggered.
   * @param {Object} opInfo.stages Methods to handle various stages of an 'update' operation.
   * @param {Object} instanceInfo Info specific to this instance of the operation.
   * @param {string} instanceInfo.callId The ID of the call the operation is performed on.
   * @return {Operation} An instance of an operation.
   */
  bottle.instanceFactory('Callstack.models.Update', (container, opInfo, instanceInfo) => {
    hasUpdateStages(opInfo.isLocal, opInfo.stages)

    // If `createFlow` is defined, use that. Otherwise, default based on `isLocal`.
    const createFlow = opInfo.createFlow ?? (opInfo.isLocal ? createLocal : createGeneric)

    // Static data for the operation.
    const { type, isLocal, stages } = opInfo

    // Instance data for the operation.
    const id = uuidV4()
    const { callId, data } = instanceInfo

    // TechDebt TODO: The Reporter's event map should be consistent so we don't have to guess for the right name.
    const eventName =
      REPORTER_OPERATION_EVENTS_MAP[type + (isLocal ? '_LOCAL' : '_REMOTE')] || REPORTER_OPERATION_EVENTS_MAP[type]
    // TODO: Should the Operation factory add the event immediately or at a later point?
    const reportEvent = bottle.container.CallReporter.getReport(callId).addEvent(eventName)

    opInfo.isNegotiation = false
    const tracker = statusTracker(bottle.container, callId, id, reportEvent.id, opInfo)

    /**
     * @typedef {Object} UpdateOperation
     * @property {string}   type The type of operation.
     * @property {boolean}  isNegotiation Whether the operation is a negotiation or not.
     * @property {boolean}  isLocal Whether the operation was started locally or not.
     * @property {Function} start The method to start processing the operation.
     * @property {string}   callId ID of the call the operation is acting on.
     * @property {string}   id ID of the operation.
     * @property {Object}   data Arbitrary information the operation needs.
     * @property {Object}   tracker The StatusTracker for the operation.
     * @property {string}   status The current status of the operation.
     * @property {Object}   reportEvent The CallReport event representing the operation.
     */
    const UpdateOperation = {
      // Operation meta-data.
      type,
      isNegotiation: false,
      isLocal,

      // Operation methods.
      /**
       * Method to start the operation.
       * @method start
       */
      start: undefined,

      // Instance specific.
      callId,
      id,
      data: { ...data },

      // Operation Tracking
      tracker,
      get status() {
        return tracker.status
      },
      reportEvent
    }

    const flow = createFlow(bottle.container, UpdateOperation, stages)
    // Add the flow methods to the Operation.
    Object.assign(UpdateOperation, flow)

    return UpdateOperation
  })
}
