import { name, reducer } from '../interface'
import createAPI from '../interface/api'
import createOperations from './operations'
import createRequests from './requests'

/**
 * ClickToCall service plugin factory.
 * Provides the SDK with the 'Click to Call' feature.
 * @method clickToCallFactory
 */
export default function clickToCallFactory(bottle) {
  // Register the component factory functions to the bottle.
  bottle.factory('ClickToCallAPI', createAPI)
  bottle.factory('ClickToCallOperations', createOperations)
  bottle.factory('ClickToCallRequests', createRequests)

  const capabilities = ['clickToCall']
  return {
    capabilities,
    name,
    reducer
  }
}
