import adapter from 'webrtc-adapter/out/adapter'

export function makeSafeForCSS(name) {
  if (!name) {
    return name
  } else {
    return name.replace(/[^a-z0-9]/g, '')
  }
}

/**
 * @returns The browser details as provided by webrtc-adapter
 */
export function getBrowserDetails() {
  return adapter.browserDetails
}
