// Call Plugin
import { getCallById } from '../../../../interface/selectors'

/**
 * Bottle wrapper for directTransfer call operation.
 * @return {Object}
 */
export default function createDirectTransfer(container) {
  return {
    // Define no-op `localChanges` stage that simply returns the parameters that `signalling` needs.
    //    This is needed because of how the LocalNegotiation flow calls these stages with parameters.
    localChanges: (callId, destination) => {
      return { destination }
    },
    signalling: (callId, { destination }) => signalling(container, callId, destination)
  }
}

/**
 * Signalling stage for a 'direct transfer' operation.
 *
 * Sends a REST request to the server to indicate the intention to transfer.
 * @method signalling
 * @param {Object} container
 * @param {string} callId
 * @param {string} destination
 * @return {Promise<undefined>}
 * @throws {BasicError} Throws an error if the REST request fails.
 */
export async function signalling(container, callId, destination) {
  const { context, CallRequests, logManager } = container
  const log = logManager.getLogger('CALL', callId)
  log.info('Performing direct transfer on call.')

  const currentCall = getCallById(context.getState(), callId)
  // Collect the information needed to make the request.
  const callInfo = {
    wrtcsSessionId: currentCall.wrtcsSessionId,
    address: destination,
    id: callId
  }

  try {
    await CallRequests.directTransferSession(callInfo)
  } catch (responseError) {
    log.info('Failed to direct transfer call.')
    throw responseError
  }

  log.info('Finished local portion of direct transfer. Waiting on remote response.')
}
