import * as actionTypes from './actionTypes'
import { handleActions } from 'redux-actions'
import { unionBy, unionWith, sortBy, isEqual } from 'lodash/fp'

const reducers = {}

reducers[actionTypes.CREATE_CONVERSATION] = {
  next(state, action) {
    return {
      ...state,
      conversations: [...state.conversations, { ...action.payload }]
    }
  }
}

reducers[actionTypes.FETCH_CONVERSATIONS_FINISHED] = {
  next(state, action) {
    // TODO: does not persist conversations unique to local state
    return {
      ...state,
      conversations: unionWith(
        (a, b) => isEqual(a.destination, b.destination) && a.type === b.type, // Comparator
        action.payload.conversations,
        state.conversations // Conversation arrays
      )
    }
  }
}

reducers[actionTypes.UPDATE_CONVERSATION] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return { ...conversation, ...action.payload }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.FETCH_MESSAGES] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            isPending: true
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.FETCH_MESSAGES_FINISHED] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: sortBy('timestamp', unionBy('messageId', action.payload.messages, conversation.messages)),
            isTypingList: [],
            isPending: false
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.SEND_MESSAGE] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.message.type
        ) {
          return {
            ...conversation,
            messages: [...conversation.messages, action.payload.message]
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.INCOMING_MESSAGE_READ] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: conversation.messages.map(message => {
              if (message.messageId === action.payload.messageId) {
                return { ...message, read: true }
              }
              return message
            })
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.SEND_MESSAGE_READ_FINISH] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: conversation.messages.map(message => {
              if (message.messageId === action.payload.messageId) {
                return { ...message, read: true }
              }
              return message
            })
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.MESSAGE_RECEIVED] = {
  next(state, action) {
    if (action.meta.newConversation) {
      return {
        ...state,
        conversations: [
          ...state.conversations,
          {
            destination: action.payload.destination,
            messages: [action.payload.message],
            type: action.meta.type,
            lastReceived: action.payload.message.timestamp
          }
        ]
      }
    } else {
      return {
        ...state,
        conversations: state.conversations.map(conversation => {
          if (isEqual(conversation.destination, action.payload.destination) && conversation.type === action.meta.type) {
            return {
              ...conversation,
              messages: [...conversation.messages, action.payload.message],
              lastReceived: action.payload.message.timestamp
            }
          }
          return conversation
        })
      }
    }
  }
}

reducers[actionTypes.SEND_MESSAGE_FINISH] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: conversation.messages.map(message => sendMessageFinishHelper(message, action))
          }
        }
        return conversation
      })
    }
  },
  throw(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: conversation.messages.filter(
              message => message.timestamp !== action.payload.timestamp && !message.isPending
            )
          }
        }
        return conversation
      })
    }
  }
}

// Remove all messages from the specified conversation.
reducers[actionTypes.CLEAR_MESSAGES] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: []
          }
        }
        return conversation
      })
    }
  }
}

reducers[actionTypes.DELETE_MESSAGE_FINISH] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.map(conversation => {
        if (
          isEqual(conversation.destination, action.payload.destination) &&
          conversation.type === action.payload.type
        ) {
          return {
            ...conversation,
            messages: conversation.messages.filter(message => message.messageId !== action.payload.messageId)
          }
        }
        return conversation
      })
    }
  },
  throw(state, action) {
    return {
      ...state,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.DELETE_CONVERSATION_FINISH] = {
  next(state, action) {
    return {
      ...state,
      conversations: state.conversations.filter(
        conversation =>
          !(isEqual(conversation.destination, action.payload.destination) && conversation.type === action.payload.type)
      )
    }
  },
  throw(state, action) {
    return {
      ...state,
      errors: state.errors.concat(action.payload)
    }
  }
}

/*
 * Combine all of reducers into a single reducer, with
 *      a default state of an empty array.
 */
const reducer = handleActions(reducers, { conversations: [], errors: [] })
export default reducer

/**
 * sendMessageFinishHelper
 *
 * A helper function to make dealing with the messages array in SEND_MESSAGE_FINISH
 * a little bit easier.
 *
 * @param {Object} message
 * @param {Object} action
 */
function sendMessageFinishHelper(message, action) {
  if (message.timestamp === action.payload.timestamp) {
    if (action.payload.error) {
      message = {
        ...message,
        sender: action.payload.sender,
        isPending: false,
        messageId: null,
        error: action.payload.error
      }
    } else {
      message = {
        ...message,
        sender: action.payload.sender,
        isPending: false,
        messageId: action.payload.messageId,
        parts: action.payload.parts
      }
    }
    if (action.payload.deliveryStatus) {
      message.deliveryStatus = action.payload.deliveryStatus
    }
  } else {
    message = { ...message }
  }
  return message
}
