// Stages.
import createStages from './join'
import validate from './validate'

import remoteAnswer from './remoteAnswer'
import remoteSuccess from './remoteSuccess'
import remoteFailure from './remoteFailure'
import joinedFailure from './joinedFailure'

import createFlow from '../../../callManager/flows/Join'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.join', () => {
    const multiFactory = bottle.container.Callstack.models.MultiCall

    /**
     * Factory function for a LocalJoin operation.
     * @method LocalJoin
     * @param {Array<Object>} callInfo List of calls affected by this operation.
     * @return {Operation} An instance of a local join operation.
     */
    function LocalJoin(callInfo) {
      const stages = createStages(bottle.container)

      return multiFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.JOIN,
          /*
           * TechDebt TODO: The join operation should be a blocking negotiation,
           *    but there are so many failure edge-cases that cause more issues
           *    with the framework. Allow other operations to happen with a join
           *    to avoid getting stuck in permanent-glare when a join fails in a`
           *    bad way.
           * The main edge-case for this is that `remoteOnly` does not track the
           *    operations. A join can fail where only the new joined call gets a
           *    'call end' notification to indicate failure, so that notification
           *    needs to "clean-up" the operation for all 3 calls.
           */
          isNegotiation: false,
          // Operation methods.
          stages: {
            validate,
            localOffer: stages.localChanges,
            signalling: stages.signalling,
            // Negotiation handler for the new, joined call.
            remoteAnswer: remoteAnswer(bottle.container),
            // Response handlers for the existing calls.
            remoteSuccess: remoteSuccess(bottle.container),
            remoteFailure: remoteFailure(bottle.container),
            joinedFailure: joinedFailure(bottle.container)
          },
          createFlow
        },
        {
          callInfo
        }
      )
    }

    return {
      local: LocalJoin
    }
  })
}
