// Stages.
import createSignalling from './consultativeTransfer'
import remoteSuccess from './remoteSuccess'
import remoteFailure from './remoteFailure'
import validate from './validate'

import createFlow from '../../../callManager/flows/ConsultativeTransfer'

import { OPERATIONS } from '../../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.consultativeTransfer', () => {
    const multiFactory = bottle.container.Callstack.models.MultiCall

    /**
     * Factory function for a LocalConsultTransfer operation.
     * @method LocalConsultTransfer
     * @param {Array<Object>} callInfo List of calls affected by this operation.
     * @return {Operation} An instance of a local consultative transfer operation.
     */
    function LocalConsultTransfer(callInfo) {
      return multiFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.CONSULTATIVE_TRANSFER,
          isNegotiation: true,
          // Operation methods.
          stages: {
            validate,
            // TODO: Don't use negotiation terms for this.
            signalling: createSignalling(bottle.container),
            remoteSuccess: remoteSuccess(bottle.container),
            remoteFailure: remoteFailure(bottle.container)
          },
          createFlow
        },
        {
          callInfo
        }
      )
    }

    return {
      local: LocalConsultTransfer
    }
  })
}
