/**
 * Promise-wrapper to time it out after a delay without resolving.
 * @method timedPromise
 * @param {Promise} promise The promise to time.
 * @param {number} [timeout=1000] The delay to wait before timing-out the promise.
 * @returns {Promise}
 */
export async function timedPromise(promise, timeout = 1000) {
  let timeoutId
  return Promise.race([
    promise,
    new Promise((resolve, reject) => {
      timeoutId = setTimeout(() => {
        reject(new Error(`Timed-out promise after ${timeout} milliseconds.`))
      }, timeout)
    })
  ]).finally(() => clearTimeout(timeoutId))
}

/**
 * Generator for a "Truncated exponential backoff" sequence.
 * Ref: wikipedia.org/wiki/Exponential_backoff#Truncated_exponential_backoff
 * @method makeTebSequence
 * @param {number} delay
 * @param {Object} [options]
 * @param {Object} [options.multiplier=2] The multiplier for how much to increase subsequent delays.
 * @param {Object} [options.delayLimit=delay*8] The maximum delay value.
 * @param {number} [options.limit] The maximum numbers to generate in the sequence.
 * @return {Generator}
 * @example
 * // Constant sequence with a call limit.
 * const sequenceOne = makeTebSequence(5, { multiplier: 1, limit: 4 })
 * sequenceOne.next() // { value: 5, done: false }
 * sequenceOne.next() // { value: 5, done: false }
 * sequenceOne.next() // { value: 5, done: false }
 * sequenceOne.next() // { value: 5, done: true }
 * sequenceOne.next() // { value: undefined, done: true }
 *
 * // Exponential sequence with a value limit.
 * const sequenceTwo = makeTebSequence(2, { multiplier: 2, delayLimit: 100 })
 * sequenceTwo.next() // { value: 2, done: false }
 * sequenceTwo.next() // { value: 4, done: false }
 * sequenceTwo.next() // { value: 8, done: false }
 * sequenceTwo.next() // { value: 16, done: false }
 * sequenceTwo.next() // { value: 32, done: false }
 * sequenceTwo.next() // { value: 64, done: false }
 * sequenceTwo.next() // { value: 100, done: false }
 * sequenceTwo.next() // { value: 100, done: false }
 */
export function* makeTebSequence(delay, options = {}) {
  const { delayLimit = delay * 8, multiplier = 2, limit } = options

  let iterations = 1
  let nextDelay = delay
  while (true) {
    if (limit && limit <= iterations) {
      return nextDelay
    } else {
      yield nextDelay
    }

    if (nextDelay < delayLimit) {
      nextDelay = delay * Math.pow(multiplier, iterations)
      nextDelay = nextDelay > delayLimit ? delayLimit : nextDelay
    }
    iterations++
  }
}

/**
 * Utility to wrap an asynchronous task to repeat until it either succeeds or times-out. Repeated attempts
 *    can have a delay between the start of each attempt that follows a configurable "truncated exponential
 *    backoff" algorithm.
 * @method startTebAttempts
 * @param {Object} options Configurations for this wrapper.
 * @param {number} [options.maxAttempts=5]
 * @param {number} [options.attemptDelay=1000] The initial delay in milliseconds between attempts.
 * @param {number} [options.delayMultiplier=1] The multiplier to increase the delay between each attempt.
 * @param {number} [options.maxDelay=5000] The maximm delay in milliseconds between attempts.
 * @param {number} [options.taskTimeout=1000] Time in milliseconds to wait for each task attempt.
 * @param {Function} task The task being wrapped.
 * @param {any} [taskParams] Parameters to be provided to the `task` parameter.
 * @return {Promise} Resolves if the task resolves. Rejects if the task never resolves after configured attempts.
 * @example
 * // Task that will succeed.
 * const taskSuccess = (message, more) => console.log(mesage, more)
 * await startTebAttempts(options, taskSuccess, 'A message', '...and more')
 * // logs to console immediately: 'A message ...and more'
 *
 *
 * // Task that will fail.
 * const taskFailure = () => throw new Error('Dummy error.')
 * try {
 *    const options = { maxRepeats: 3, attemptDelay: 100, delayMultiplier: 1 }
 *    await startTebAttempts(options, taskFailure)
 * } catch (err) {
 *    // `taskFailure` will be called 3 times: immediately, after 100ms, then after another 100ms
 *    // err === new Error('Dummy error.)
 * }
 *
 *
 * // Task that will timeout.
 * const taskTimeout = () => new Promise(resolve => {})
 * try {
 *    const options = { taskTimeout: 5, maxRepeats: 4, attemptDelay: 10, delayMultiplier: 2 }
 *    await startTebAttempts(options, taskTimeout)
 * } catch (err) {
 *    // `taskTimeout` will be called 4 times: immediately, after 10ms, then after 20ms, then
 *    //    after 80ms.
 *    // err === new Error('Timed-out after 5 milliseconds.')
 * }
 */
export function startTebAttempts(
  { taskTimeout = 1000, attemptDelay = 1000, delayMultiplier = 1, maxDelay = 5000, maxAttempts = 5 },
  task,
  ...taskParams
) {
  let nextTimeout
  // Create the sequence of delays between attempts. Also sets the
  //    limit for max attempts.
  const delays = makeTebSequence(attemptDelay, {
    delayLimit: maxDelay,
    multiplier: delayMultiplier,
    limit: maxAttempts
  })

  return new Promise((resolve, reject) => {
    attemptTask({ resolve, reject })
  })

  /**
   * Somewhat-recursive function to attempt performing the task.
   * Base-case depends on the scoped `delays` instead of parameters.
   * @method attemptTask
   * @param {Object} promise The top-level promise functions.
   * @param {Function} promise.resolve
   * @param {Function} promise.reject
   */
  async function attemptTask(promise) {
    const next = delays.next()
    const lastTask = next.done

    if (!lastTask) {
      // Start the timer for the next task already. Avoids needing to
      //    time how long the current attempt takes before starting
      //    to wait for the next one.
      nextTimeout = setTimeout(attemptTask, next.value, promise)
    }

    try {
      // Ensure `task` is timed-out before the next attempt starts.
      const lesserDelay = next.value < taskTimeout ? next.value : taskTimeout
      const result = await timedPromise(task(...taskParams), lesserDelay)
      clearTimeout(nextTimeout)
      promise.resolve(result)
    } catch (err) {
      if (lastTask) {
        promise.reject(err)
      } else {
        // Else wait for the next setTimeout to trigger.
      }
    }
  }
}
