import createOperation from './onWebsocketOverridden'

// Notifications Plugin
import { NOTIFICATION_RECEIVED } from '../../../../notifications/interface/actionTypes'

/**
 * Handler for when the websocket gets overridden by another client.
 */
export default function registerFeature(bottle) {
  // Register the operation to the bottle immediately.
  bottle.factory('SubscriptionOperations.onWebsocketOverridden', container => {
    return createOperation(bottle.container)
  })

  // Register the listener for triggering the operation after the SDK is initialized.
  //    This ensures that the Notification plugin is setup.
  bottle.defer(container => {
    // Take notifications about the websocket being 'overridden'.
    function receiveOverriddenNotificationPattern(action) {
      return (
        action.type === NOTIFICATION_RECEIVED &&
        action.payload.notificationMessage &&
        action.payload.notificationMessage.eventType === 'websocketOverridden'
      )
    }

    container.Notifications.takeEveryAction(receiveOverriddenNotificationPattern, action => {
      container.SubscriptionOperations.onWebsocketOverridden()
    })
  })
}
