import * as actionTypes from './actionTypes'

/**
 * Represents a request to POST clickToCall data.
 * @method clickToCall
 * @param  {string} callId
 * @param  {string} caller
 * @param  {string} callee
 * @return {Action} A redux action.
 */
export function clickToCall(callId, caller, callee) {
  return {
    type: actionTypes.CLICK_TO_CALL,
    payload: {
      callId,
      caller,
      callee
    }
  }
}

/**
 * Represents that a response was received for a clickToCall request.
 * @method clickToCallFinish
 * @param  {string} callId
 * @param  {string} caller
 * @param  {string} callee
 * @param  {number} requestTime time that the request was made at
 * @param  {Boolean}  [error] A parameter to indicate if there was an issue.
 * @return {Action} A redux action.
 */
export function clickToCallFinish({ callId, caller, callee, requestTime, error }) {
  if (error) {
    return {
      type: actionTypes.CLICK_TO_CALL_FINISH,
      error: true,
      payload: {
        error
      }
    }
  } else {
    return {
      type: actionTypes.CLICK_TO_CALL_FINISH,
      error: false,
      payload: {
        callId,
        caller,
        callee,
        requestTime
      }
    }
  }
}
